
#canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
}

html, body {
  height: 100%;
  overflow: hidden;
}

#location {
  color: white;
  padding: 6px;
  border-top-left-radius: 6px;
  font-size: 8px;
  position: fixed;
  right: 0;
}

.PanelHeaderContent__status,
.PanelHeaderContent__children,
.PanelHeader__left {
  color: #fff;
}

.PanelHeaderContent__status {
  color: #eee;
}

#pointer {
  font-size: 50px;
  width: 2em;
  height: 2em;
  background: transparent;
  box-shadow: inset 0 0 0 1px red;
  top: 50%;
  left: 50%;
  margin: -1em;
  position: fixed;
}

#controls {
  position: fixed;
  left: 0px;
  border-top-right-radius: 6px;
}

#location,
#controls,
.PanelHeader--trnsp .PanelHeader__fixed {
  background: rgba(0, 0, 0, .4);
}

#location,
#controls {
  bottom: 0px;
  bottom: env(safe-area-inset-bottom);
}

#root:after {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: block;
  content: '';
  background: rgba(0, 0, 0, .4);
  z-index: 2;
  height: 0px;
  height: env(safe-area-inset-bottom);
}

#controls svg {
  color: white;
}
